.story-section {
	position: relative;
	padding: 10rem 0;
	min-height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	@include max-lg {
		padding: 8rem 0;
	}
	@include max-md {
		padding: 5rem 0;
	}
	.story-ornament {
		position: relative;
		align-items: center;
		justify-content: center;
		margin-bottom: 3rem;
		.wayang-deco {
			width: 90px;
			height: auto;
			@include max-md {
				width: 82px;
			}
		}
	}
	.story-content {
		position: relative;
		border-radius: var(--border-radius);
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		align-items: center;
	}
	.story-description {
		position: relative;
		text-align: center;
		color: $color-custom;
		* {
			position: relative;
		}
	}
	.image-wrapper {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		.story-background {
			position: relative;
			object-fit: cover;
			background-position: center;
			width: 100%;
			height: 100%;
		}
	}
}
