@keyframes zoomOutBgWrap {
	0% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
	40% {
		transform: scale3d(1, 1, 1);
	}
	60% {
		transform: scale3d(1, 1, 1);
	}
	100% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
}

@keyframes zoomOutTree {
	0% {
		transform: scale3d(1, 1, 1);
	}
	40% {
		transform: scale3d(0.8, 0.8, 0.8);
	}
	60% {
		transform: scale3d(0.8, 0.8, 0.8);
	}
	100% {
		transform: scale3d(1, 1, 1) translateX(-2rem);
	}
}

@include mx-mn(767px, 392px) {
	@keyframes zoomOutTree {
		0% {
			transform: scale3d(1, 1, 1);
		}
		40% {
			transform: scale3d(0.9, 0.9, 0.9);
		}
		60% {
			transform: scale3d(0.9, 0.9, 0.9);
		}
		100% {
			transform: scale3d(1, 1, 1);
		}
	}
}

@include max-xs {
	@keyframes zoomOutTree {
		0% {
			transform: scale3d(1, 1, 1);
		}
		40% {
			transform: scale3d(0.8, 0.8, 0.8);
		}
		60% {
			transform: scale3d(0.8, 0.8, 0.8);
		}
		100% {
			transform: scale3d(1, 1, 1);
		}
	}
}

@keyframes zoomOutTree2 {
	0% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
	40% {
		transform: scale3d(1, 1, 1);
	}
	60% {
		transform: scale3d(1, 1, 1);
	}
	100% {
		transform: scale3d(1.2, 1.2, 1.2) translateX(1rem);
	}
}

@keyframes zoomOutTree3 {
	0% {
		transform: scale3d(1.1, 1.1, 1.1);
	}
	40% {
		transform: scale3d(1, 1, 1);
	}
	60% {
		transform: scale3d(1, 1, 1);
	}
	100% {
		transform: scale3d(1.2, 1.2, 1.2) translateX(-1rem);
	}
}

@keyframes zoomOutTreeLeft {
	0% {
		transform: scale3d(1.2, 1.2, 1.2);
	}
	40% {
		transform: scale3d(1, 1, 1);
	}
	60% {
		transform: scale3d(1, 1, 1);
	}
	100% {
		transform: translate3d(-10%, 5%, 0);
	}
}

@include max-lg {
	@keyframes zoomOutTreeLeft {
		0% {
			transform: scale3d(1.2, 1.2, 1.2);
		}
		40% {
			transform: scale3d(1, 1, 1);
		}
		60% {
			transform: scale3d(1, 1, 1);
		}
		100% {
			transform: translate3d(-10%, 10%, 0);
		}
	}
}

@keyframes zoomOutTreeRight {
	0% {
		transform: scale3d(1.2, 1.2, 1.2);
	}
	40% {
		transform: scale3d(1, 1, 1);
	}
	60% {
		transform: scale3d(1, 1, 1);
	}
	100% {
		transform: translateX(10%);
	}
}
